import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import { BrowserRouter as Router, HashRouter } from 'react-router-dom';

ReactDOM.render(
	<HashRouter basename='/'>
		<React.StrictMode>
			<App />
		</React.StrictMode>
	</HashRouter>,
	document.getElementById('root'),
);
